<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="card-body">
    <VForm v-slot="{ meta }" @submit="updatePassword">
      <table class="table align-middle table-light table-striped-columns">
        <tbody>
          <!-- 密碼 -->
          <tr>
            <th scope="row">密碼<br />Password</th>
            <td>
              <VField name="password" type="password" :rules="passwordRules" v-model="password" />
              <div><ErrorMessage name="password" class="text-danger" /></div>
            </td>
          </tr>
          <!-- 密碼確認 -->
          <tr>
            <th scope="row">密碼確認<br />Password Confirm</th>
            <td>
              <VField
                name="confirmPassword"
                type="password"
                :rules="confirmPasswordRules"
                v-model="confirmPassword"
              />
              <div><ErrorMessage name="confirmPassword" class="text-danger" /></div>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-center">
        <button
          type="submit"
          :class="{ 'btn-info': !meta.valid }"
          :disabled="!meta.valid"
          class="btn btn-primary rounded-0"
        >
          Update
        </button>
      </div>
    </VForm>
  </div>
</template>

<script>
import { apiUpdatePassword } from '@/assets/javascript/api';

export default {
  data() {
    return {
      password: '',
      confirmPassword: '',
      passwordRules: [
        (value) => !!value || 'Please type password.',
        (value) => (value && value.length >= 6) || 'minimum 6 characters',
      ],
      confirmPasswordRules: [
        (value) => !!value || 'type confirm password',
        (value) => value === this.password || 'The password confirmation does not match.',
      ],
    };
  },
  methods: {
    updatePassword(e) {
      apiUpdatePassword(e)
        .then(() => {
          this.$emitter.emit('messageModal', { status: true, message: 'Password Successfully Modified' });
        })
        .catch((error) => {
          this.$emitter.emit('messageModal', { status: false, message: error.response.data.message });
        });
    },
  },
};
</script>
