import axios from 'axios';

const api = process.env.VUE_APP_API;

// users
export const apiUserSignUp = (data) => axios.post(`${api}/user/sign_up`, data);
export const apiUserSignIn = (data) => axios.post(`${api}/user/sign_in`, data);
export const apiGetProfile = () => axios.get(`${api}/user/profile`);
export const apiUpdateProfile = (data) => axios.patch(`${api}/user/profile`, data);
export const apiUpdatePassword = (data) => axios.post(`${api}/user/updatePassword`, data);

// products
export const apiGetProducts = () => axios.get(`${api}/products`);

// carts
export const apiGetCarts = () => axios.get(`${api}/carts`);
export const apiPostCart = (data) => axios.post(`${api}/cart`, data);
export const apiDeleteCart = (data) => axios.delete(`${api}/cart`, data);
export const apiPatchCart = (data) => axios.patch(`${api}/cart`, data);
